<template>
    <div class="container">
        <ul class="list-group list-group-hover">
            <TodoItem
                :todos="incompleteTodos"
                :setCurrentTodo="setCurrentTodo"
                :toggleTodoCompletion="toggleTodoCompletion"
            />
        </ul>
        <button class="btn btn-outline-secondary my-3" @click="toggleShowCompleted">
            <i :class="{'bi bi-chevron-down' : showCompleted, 'bi bi-chevron-right':!showCompleted}">
            </i> Completed {{ completedCount }}
        </button>
        <ul v-show="showCompleted" class="list-group list-group-hover">
            <TodoItem
                :todos="completedTodos"
                :setCurrentTodo="setCurrentTodo"
                :toggleTodoCompletion="toggleTodoCompletion"
            />
        </ul>
    </div>
    <TodoModal
        v-if="currentTodo !== null"
        :todo="currentTodo"
        :deleteTodo="deleteTodo"
        :toggleTodoCompletion="toggleTodoCompletion"
    />
    <div class="fixed-bottom bg-dark" style="height: 55px;">
        <div class="container input-group" style="height: 45px;">
            <input v-model="newTodo" @keyup.enter="addTodo"
                type="text"
                list="itemOptions"
                class="form-control" 
                placeholder="Add a new item..."
            />
            <datalist id="itemOptions">
                <option v-for="(name, index) in uniqueItems" :key="index" :value="name" >
                </option>
            </datalist>
            <button @click="addTodo" class="btn btn-secondary" type="button"><i class="bi bi-file-arrow-up-fill"></i></button>
        </div>
    </div>
</template>

<script>
import TodoItem from './TodoItem.vue';
import TodoModal from './TodoModal.vue';

export default {
    data() {
        return {
            newTodo: '',
            todos: JSON.parse(localStorage.getItem('todoItems')) || [],
            showCompleted: false,
            currentTodo: {name:"",done:false,id:""}
        };
    },
    components: {
        TodoItem,
        TodoModal
    },
    computed: {
        completedTodos() {
          return this.todos.filter(todo => todo.done);
        },
        incompleteTodos() {
          return this.todos.filter(todo => !todo.done);
        },
        completedCount() {
          return this.completedTodos.length;
        },
        uniqueItems(){
            return [...new Set(this.todos.map(todo => todo.name))].sort()
        }
      },
    methods: {
        capitalizeFirstLetter(string) {
            string = string.trim();
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
        toggleShowCompleted(){
            this.showCompleted = !this.showCompleted;
        },
        addTodo() {
            if (this.newTodo.trim()) {
                this.todos.push({ id: Date.now(), name: this.capitalizeFirstLetter(this.newTodo), done: false });
                this.newTodo = '';
                this.saveTodos();
            }
        },
        toggleTodoCompletion(todo) {
          // Temporarily mark the todo as not completed
          const originalCompletedState = todo.done;
          todo.done = !originalCompletedState;

          // Delay the actual save operation
          setTimeout(() => {
            todo.done = originalCompletedState;
            this.saveTodos();
          }, 200)
        },
        setCurrentTodo(todo){
            this.currentTodo = todo;
        },
        deleteTodo(id) {
            this.todos = this.todos.filter(todo => todo.id !== id );
            this.saveTodos();
        },
        saveTodos() {
            localStorage.setItem('todoItems', JSON.stringify(this.todos));
        }
    }
};
</script>

<style>
.btn {
  z-index: 1;
}
.fixed-bottom {
  opacity: .95;
}
</style>
