<template>
    <div id="editTodoModal" class="modal fade" tabIndex="-1">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Edit item</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal"
                            aria-label="Close">
                    </button>
                </div>
                <div class="modal-body">
                    <div class="d-flex align-items-center">
                        <input type="checkbox"
                                class="form-check-input me-2 rounded-circle"
                                v-model="todoCopy.done"
                                @change="() => toggleTodoCompletion(todoCopy)"
                        />
                        <input
                            type="text"
                            class="form-control"
                            v-model="todoCopy.name"
                        />
                    </div>
                </div>
                <div class="modal-footer">
                    <button @click="deleteTodo(todo.id)" type="button" class="btn btn-danger" data-bs-dismiss="modal">
                        <i class="bi bi-trash3"></i>
                        Delete
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        todo: {
            type: Object,
            required: true
        },
        deleteTodo: {
            type: Function,
            required: true
        },
        toggleTodoCompletion: {
            type: Function,
            required: true
        }
    },
    data() {
        return {
            todoCopy: this.todo
        };
    },
    watch: {
        todo(val){
            this.todoCopy = val;
        }
    }
};
</script>