<template>
    <li v-for="(todo) in todos" :key="todo.id" class="list-group-item d-flex justify-content-between">
        <div class="d-flex align-items-center">
            <input :id="'todo-' + todo.id" type="checkbox" class="form-check-input me-2 rounded-circle"
                v-model="todo.done" @change="() => toggleTodoCompletion(todo)"/>
            <label :for="'todo-' + todo.id" :class="['form-check-label stretched-link', { completed: todo.done }]">
                {{ todo.name }}
            </label>
        </div>
        <button data-bs-toggle="modal" data-bs-target="#editTodoModal" class="btn" @click="() => setCurrentTodo(todo)"><i class="bi bi-pencil"></i></button>
    </li>
</template>

<script>
export default {
    props: {
        todos: {
            type: Array,
            required: true
        },
        setCurrentTodo: {
            type: Function,
            required: true
        },
        toggleTodoCompletion: {
            type: Function,
            required: true
        }
    }
};
</script>
<style>
.completed {
    text-decoration: line-through;
}
</style>
